<script>
import ThreeClerkComponent from "@/components/ThreeClerkComponent.vue";
import OneClassComponent from "../components/OneDiagramComponent.vue";
import ThreeCustomerComponent from "../components/ThreeCustomerComponent.vue";
import OneReservationComponent from "../components/OneReservationComponent.vue";
import ThreePersonComponent from "../components/ThreePersonComponent.vue";
import OneVehicleComponent from "../components/OneVehicleComponent.vue";
import OneRentalSystemComponent from "@/components/OneRentalSystemComponent.vue";
import OneInstructionsComponent from "../components/OneInstructionsComponent.vue";
import ThreeTestDriverComponent from "@/components/ThreeTestDriverComponent.vue";

export default {
  components: {
    ThreeClerkComponent,
    OneClassComponent,
    ThreeCustomerComponent,
    ThreePersonComponent,
    OneReservationComponent,
    OneVehicleComponent,
    OneRentalSystemComponent,
    OneInstructionsComponent,
    ThreeTestDriverComponent,
  },

  data() {
    return {
      componentSelect: "OneInstructionsComponent",
      testDriveComplete: false,
      personComplete: false,
      customerComplete: false,
      clerkComplete: false,
    };
  },
  methods: {
    testDriveCompleteFunc(value) {
      this.testDriveComplete = true;
      this.componentSelect = "ThreePersonComponent";
    },
    personCompleteFunc(value) {
      this.personComplete = true;
      this.componentSelect = "ThreeCustomerComponent";
    },
    customerCompleteFunc(value) {
      this.customerComplete = true;
      this.componentSelect = "ThreeClerkComponent";
    },
    clerkCompleteFunc(value) {
      this.clerkComplete = true;
      this.componentSelect = "OneVehicleComponent";
    },
  },
}
</script>

<template>
  <main class="app">
    <div class="buttons">
      <button @click="componentSelect = 'OneInstructionsComponent'"
              :style="componentSelect === 'OneInstructionsComponent' ? 'background-color: #55acee' : ''">1. Instructions
      </button>
      <button @click="componentSelect = 'ThreeTestDriverComponent'"
              :style="componentSelect === 'ThreeTestDriverComponent' ? 'background-color: #55acee' : ''">2. Test Driver
      </button>
      <button @click="componentSelect = 'ThreePersonComponent'"
              :disabled="!testDriveComplete"
              :style="componentSelect === 'ThreePersonComponent' ? 'background-color: #55acee' : ''">3. Person
      </button>
      <button @click="componentSelect = 'ThreeCustomerComponent'"
              :disabled="!personComplete"
              :style="componentSelect === 'ThreeCustomerComponent' ? 'background-color: #55acee' : ''">4. Customer
      </button>
      <button @click="componentSelect = 'ThreeClerkComponent'"
              :disabled="!customerComplete"
              :style="componentSelect === 'ThreeClerkComponent' ? 'background-color: #55acee' : ''">5. Clerk
      </button>
      <button @click="componentSelect = 'OneVehicleComponent'"
              :style="componentSelect === 'OneVehicleComponent' ? 'background-color: #55acee' : ''">6. Vehicle
      </button>
      <button @click="componentSelect = 'OneReservationComponent'"
              :style="componentSelect === 'OneReservationComponent' ? 'background-color: #55acee' : ''">7. Reservation
      </button>
      <button @click="componentSelect = 'OneRentalSystemComponent'"
              :style="componentSelect === 'OneRentalSystemComponent' ? 'background-color: #55acee' : ''">8. Rental System
      </button>
      <button @click="componentSelect = 'OneClassComponent'"
              :style="componentSelect === 'OneClassComponent' ? 'background-color: #55acee' : ''">9. Class Diagram
      </button>

    </div>

    <KeepAlive>
      <component
          @instructions-read="componentSelect = 'ThreeTestDriverComponent'"
          @test-driver-complete="testDriveCompleteFunc"
          @person-complete="personCompleteFunc"
          @customer-complete="customerCompleteFunc"
          @clerk-complete="clerkCompleteFunc"
          v-bind:is="componentSelect"
      />
    </KeepAlive>
  </main>
  <footer v-show="testDriveComplete && personComplete && customerComplete && clerkComplete">
    <div id="buttons" class="relative">
      <button class="navitem" @click="$router.push('/congratulations')">Next</button>
    </div>
  </footer>
</template>

<style>
.app {
  background-color: #f9fafe;
  min-height: 1024px;
}

button {
  margin-top: 30px;
  border: none;
  padding: 20px;
  margin-left: 5px;
  background-color: #2c3e50;
  color: white;
  width: 500px;
}

button:first-of-type {
  margin-left: 30px;
}

button:disabled {
  background-color: #7c8e9d;
}

.buttons {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  margin-top: 0;
}
</style>
 